@import url("https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap");
* {
  scroll-behavior: smooth;
  box-sizing: border-box;
  margin: 0;
  font-family: "Indie Flower", cursive;
  font-weight: 400;
  font-style: normal;
}

body {
  background: rgb(25, 41, 47);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.container {
  padding: 20px;
  max-width: 1140px;
  margin: 0 auto;
}

header,
footer {
  background: #f3ce00;
}

h1,
h2 {
  margin: 0;
  text-align: center;
}

h1 {
  font-size: 60px;
}

main {
  color: #f3ce00;
  text-align: center;
  margin: 0 auto;
}

.content {
  height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 30px;
}

i {
  color: #f3ce00;
}

i.win {
  color: rgb(255, 0, 0);
}

.open {
  display: block;
}

.close {
  display: none;
}

.modal {
  color: rgb(25, 41, 47);
  font-size: 32px;
  text-align: center;
}
.modal-window {
  padding: 50px;
  height: 260px;
  width: 380px;
  margin: 0;
  z-index: 999;
  border-radius: 20px;
  box-shadow: 0 0 40px 50px rgb(25, 41, 47);
  background: #f3ce00;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.modal-btns {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
.modal-btn {
  all: unset;
  padding: 20px;
  font-size: 24px;
  cursor: pointer;
  transition: all 0.5s ease;
}
.modal-btn:hover {
  scale: 1.2;
}
.modal-btn i {
  color: rgb(25, 41, 47);
}

.board {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(3, 100px);
  gap: 5px;
  margin: 10px auto;
  color: #f3ce00;
  background-color: #f3ce00;
}

.cell {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-size: 200%;
  background-color: rgb(25, 41, 47);
  cursor: pointer;
}
.cell.disabled {
  pointer-events: none;
}

.score {
  margin: 20px auto;
  height: 200px;
}

footer .footer-wraper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
footer .footer-item {
  margin: 0 auto;
}
footer .link-img {
  height: 40px;
  transition: all 0.5s ease;
}
footer .link-img:hover {
  scale: 1.05;
}
footer .rsschool {
  width: 110px;
}
footer .year {
  width: 110px;
  text-align: right;
  font-size: 24px;
}

@media (max-width: 500px) {
  body {
    font-size: 12px;
  }
  .container {
    padding: 0;
  }
  .board {
    grid-template-columns: repeat(3, 75px);
    grid-template-rows: repeat(3, 75px);
    gap: 5px;
    margin: 10px auto;
  }
  .cell {
    width: 75px;
    height: 75px;
  }
  .modal {
    font-size: 16px;
  }
  .modal-window {
    padding: 5px;
    height: 140px;
    width: 280px;
    gap: 5px;
  }
  .modal-btns {
    display: flex;
    justify-content: space-between;
    gap: 5px;
  }
  .modal-btn {
    all: unset;
    padding: 20px;
    font-size: 24px;
    cursor: pointer;
    transition: all 0.5s ease;
  }
  .modal-btn:hover {
    scale: 1.2;
  }
  .modal-btn i {
    color: rgb(25, 41, 47);
  }
  .score {
    margin: 5px auto;
    height: 100px;
  }
  footer .rsschool,
  footer .year {
    display: none;
  }
  footer .footer-item {
    margin: 0 auto;
  }
  footer .link-img {
    height: 30px;
  }
}
@media (max-width: 380px) {
  body {
    font-size: 10px;
  }
}